.Icon:hover {
    cursor: pointer;
}

.Icon-check {
    display: -ms-flexbox;
    display: flex;
    width: 24px;
    height: 24px;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.Icon-check--active{
    color: white;
}

.TodoItem-p--completed{
    text-decoration: line-through;
}

.Icon-delete:hover{
    color: #F24C3D;
}
.Icon-check:hover{
    color: wheat;
}


