.addButton{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.addButton button{
    width: 140px;
    height: 40px;
    border-radius: 12px;
    background: #F24C3D;
    z-index: 1;

}
.addButton button:hover{
    width: 160px;
    height: 60px;
    cursor: pointer;
}



