.Loading-container{
    display: -ms-flexbox;
    display: flex;
    width: 350px;
    height: 50px;
    text-align: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-radius: 10px;
    margin: 10px;
    padding: 0px 15px 0px 15px;
}
.Loading-container,
.Loading-Check,
.Loading-delete {
  background: -webkit-gradient(linear, left top, right top, from(#075d54), to(#969f9e));
  background: -webkit-linear-gradient(left, #075d54, #969f9e);
  background: -o-linear-gradient(left, #075d54, #969f9e);
  background: linear-gradient(90deg, #075d54, #969f9e);
  background-size: 400% 400%;
  -webkit-animation: loadingAnimation 3s ease-in-out infinite;
          animation: loadingAnimation 3s ease-in-out infinite;
}

@-webkit-keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes loadingAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

