.counter-loading {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.counter-loading h1{
    font-size: 40px;
    font-weight:200;
    text-align: center;
    background: -webkit-gradient(linear, left top, right top, from(#075d54), to(#969f9e));
    background: -webkit-linear-gradient(left, #075d54, #969f9e);
    background: -o-linear-gradient(left, #075d54, #969f9e);
    background: linear-gradient(90deg, #075d54, #969f9e);
    background-size: 400% 400%;
    -webkit-animation: loadingAnimation 3s ease-in-out infinite;
            animation: loadingAnimation 3s ease-in-out infinite;
    border-radius:20px ;
    padding-top: 20px;
    padding-bottom: 20px;
    width: 600px;
}
.counter-loading span{
    font-weight: bold;
}

@media (max-width: 640px) {
  
    .counter-loading h1{
        font-size: 28px;
        font-weight:280;
        width: 380px;
    }
}


